import React from 'react';
import './ForgotPassword.scss';
import Header from '../../Header/components/Header.js';
import { Field, reduxForm } from 'redux-form';
import _ from 'lodash';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';
import notRobotImg from '../assets/not-robot-img.png';

class ForgotPasswordContainer extends React.Component{
  constructor(props){
    super(props);
    const { handleSubmit } = this.props;
  }

  handleSubmit = values => {
    values.preventDefault();
    const { touch} = this.props;
    const errors= validate(values);
    if(errors){
      _.forEach(errors, (msg,field) => {
        touch(field);
      });
    }
  }

  renderField = ({input,label,type,placeholder, meta: { touched, error, warning }}) => (
    <div className="form-group">
      <label htmlFor="rePass">{label}</label>
      <input {...input} className="form-control" placeholder={placeholder} type={type} />
      {touched && error && <span className="error-msg-text">{error}</span>}

    </div>
)

  goToIndex = () => {
      this.props.router.push('/');
  }

  goToLogin = () => {
    this.props.router.push('/login/true');
  }

  render() {
      return (
          <div className="forgot-form">
              <Header goHome={this.goToIndex} isLoggedIn={false} />

              <div className="container history-container">
                  <div className="row">
                      <div className="offset-1 col-10">
                              <div className="step-section">
                                  <h4 className="step-text">Reset Password</h4>
                              </div>
                              <div className="password-section">
                              <form onSubmit={this.handleSubmit}>
                                <div className="form-group">
                                  <Field className="form-control" placeholder="**********" label="Password" name="password" component={this.renderField} type="password" />
                                </div>
                                <div className="form-group">
                                  <Field className="form-control" placeholder="**********" label="Password" name="secondPassword" component={this.renderField} type="password" />
                                </div>
                                <button onClick={this.goToLogin} className="btn btn-green btn-checkout">RESET AND LOGIN</button>
                              </form>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      );
  }

}

const validate = values => {
  const errors = {}
  if (!values.password) {
    errors.password = 'Required'
  }
  if (!values.secondPassword) {
    errors.secondPassword = 'Required';
  }
  if(values.password && values.secondPassword && (values.password != values.secondPassword)){
    errors.password = "Both the passwords do not match";
    errors.secondPassword = "Both the passwords do not match";
  }
  return errors
}

export default reduxForm({form: 'forgotPassword',validate})(ForgotPasswordContainer)



